import { css, styled, type StyledProps } from '../../utils/styled';
import { color, excludeProps } from '../../utils/theme';

type NamedColor = Parameters<typeof color>[0];
export type SvgIconColor = NamedColor | 'currentColor' | 'inherit';

type SvgProps = {
    color?: SvgIconColor;
    fill?: SvgIconColor;
    stroke?: SvgIconColor;
    size?: string;
};

const reservedKeywords = ['currentColor', 'inherit'];

const colorTypesDefaults = {
    color: 'inherit',
    fill: 'currentColor',
    stroke: undefined,
};

const colorHelper = (
    colorType: keyof typeof colorTypesDefaults,
    inputColor: SvgIconColor | undefined,
    props: StyledProps<unknown>,
) =>
    inputColor && !reservedKeywords.includes(inputColor)
        ? color(inputColor as NamedColor)(props)
        : colorTypesDefaults[colorType];

const commonStyles = css<SvgProps>`
    pointer-events: all;
    box-sizing: border-box;
    color: ${({ color: svgColor, ...props }) => colorHelper('color', svgColor, props)};
    fill: ${({ fill, ...props }) => colorHelper('fill', fill, props)};
    stroke: ${({ stroke, ...props }) => colorHelper('stroke', stroke, props)};
    font-size: ${(props) => props.size};
`;

const commonExcludedProps = ['fill', 'color', 'stroke'];
export const SvgRoot = styled.svg.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
    width: 1em;
    height: 1em;
`;

export const SvgIconPath = styled.path.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconRect = styled.rect.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconG = styled.g.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconCircle = styled.circle.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconLinearGradient = styled.linearGradient.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconDefs = styled.defs.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconStop = styled.stop.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;

export const SvgIconClipPath = styled.clipPath.withConfig<SvgProps>({
    shouldForwardProp: excludeProps(commonExcludedProps),
})`
    ${commonStyles};
`;
